body {
  background-color: #17181F;
  color: #aaa;
}


*{
  box-sizing: border-box;
  font-family: sans-serif;
}

main {
  max-width: 400px;
  margin: 20px auto;

}
form {
  border: 3px solid #30313D;
  border-radius: 10px;

 display: flex;
 padding: 5px;

}
form input[type='text']{
  background:none;
  border: 0;
  padding: 0 8px;
  display: block;
  color: white;
  width: 100%;
}

form button {
  background-color: #61DAFB;
  border: 0;
  border-radius: 6px;
}



 div.task {
  background-color: #30313D;
  padding: 5px 9px;
  margin-top: 5px;
  border-radius: 10px;
  display: flex;
 }

div.task svg {
  height: 20px;
  margin-right: 4px;
}